import { Component, OnInit, ElementRef, ViewChild,AfterViewChecked } from '@angular/core';

import { ToastrService } from 'ngx-toastr';
import { ServiceAppService } from '../../service-app.service';
import { SocketService } from '../../socket.service';
import { Router } from '@angular/router';
import { Cookie } from 'ng2-cookies/ng2-cookies';

@Component({
  selector: 'app-chatbox',
  templateUrl: './chatbox.component.html',
  styleUrls: ['./chatbox.component.css']
})
export class ChatboxComponent implements OnInit {

  @ViewChild('scrollMe', { read: ElementRef }) 
  
  public scrollMe: ElementRef;
  
  
  public authToken: any;
  public userInfo: any;
  public receiverId: any;
  public details: any;
  public userList: any = [];
  public messageText: any;
  public messageList:any=[];
  public receiverName:any;
  public disconnectedSocket: boolean;
  public pageValue:number=0;
  public loadingPreviousChat=false;
  public scrollToChatTop:boolean=false;

  constructor(
    public AppService: ServiceAppService,
    public SocketService: SocketService,
    public router: Router,
    public toastr:ToastrService

  ) { 
    this.receiverId = Cookie.get('receiverId');

    this.details = Cookie.get('details');
  }

  ngOnInit() {
    this.authToken = Cookie.get('authtoken');
    this.userInfo = this.AppService.getUserInfoLocalStorage();
   // this.checkStatus();
    this.verifyUserConfirmation();
    this.getMessageFromAUser();
   
  }
/*
  public checkStatus: any = () => {

    if (Cookie.get('authtoken') === undefined || Cookie.get('authtoken') === '' || Cookie.get('authtoken') === null) {

      this.router.navigate(['/']);

      return false;

    } else {

      return true;

    }

  } // end checkStatus
  */

  public verifyUserConfirmation: any = () => {

    this.SocketService.verifyUser()
      .subscribe((data) => {

        this.disconnectedSocket = false;

        this.SocketService.setUser(this.authToken);
        this.getOnlineUserList()

      });
    }


    public getOnlineUserList :any =()=>{

      this.SocketService.onlineUserList()
        .subscribe((userList) => {
  
          this.userList = [];
  
          for (let x in userList) {
  
            let temp = { 'userId': x, 'name': userList[x], 'unread': 0, 'chatting': false };
  
            this.userList.push(temp);          
  
          }
          
          console.log(this.userList);
  
        }); // end online-user-list
  

}


public sendMessageUsingKeypress: any = (event: any) => {

  if (event.keyCode === 13) { // 13 is keycode of enter.

    this.sendMessage();

  }

} // end sendMessageUsingKeypress

public sendMessage: any = () => {

  if(this.messageText){

    let chatMsgObject = {
      senderName: this.userInfo.firstName + " " + this.userInfo.lastName,
      senderId: this.userInfo.userId,
      receiverName: Cookie.get('receiverName'),
      receiverId: Cookie.get('receiverId'),
      message: this.messageText,
      createdOn: new Date()
    } // end chatMsgObject
    console.log(chatMsgObject);
    this.SocketService.SendChatMessage(chatMsgObject)
    this.pushToChatWindow(chatMsgObject)
    

  }
  else{
    this.toastr.warning('text message can not be empty')

  }

} // end sendMessage

public pushToChatWindow : any =(data)=>{

  this.messageText="";
  this.messageList.push(data);
  this.scrollToChatTop = false;
  console.log("scroll top "+this.scrollToChatTop);

}// end push to chat window

public userSelectedToChat: any = (id, name) => {

  console.log("setting user as active") 

  // setting that user to chatting true   
  this.userList.map((user)=>{
      if(user.userId==id){
        user.chatting=true;
      }
      else{
        user.chatting = false;
      }
  })

  Cookie.set('receiverId', id);

  Cookie.set('receiverName', name);

  this.messageList=[];
  this.receiverName = name;

    this.receiverId = id;

    this.pageValue = 0;

console.log("reciver id:-"+id+"name:-"+name);
let chatDetails = {
  userId: this.userInfo.userId,
  senderId: id
}


this.SocketService.markChatAsSeen(chatDetails);
this.getPreviousChatWithAUser();
  
} // end userBtnClick function


public getPreviousChatWithAUser(){
  //console.log("message sice "+this.messageList.slice());
  let previousData = (this.messageList.length > 0 ? this.messageList.slice() : []);
  console.log("prvious data "+previousData);
  this.SocketService.getChat(this.userInfo.userId, this.receiverId, this.pageValue * 2)
    .subscribe((apiResponse) => {

      
      


      if (apiResponse.status == 200) {
        console.log("prvious data after it "+previousData);
        console.log("from the previous chat:-"+apiResponse.data.concat(previousData));
        this.messageList = apiResponse.data.concat(previousData);

      } else {

        this.messageList = previousData;
        this.toastr.warning('No Messages available')

       

      }

     this.loadingPreviousChat = false;

    }, (err) => {

      this.toastr.error('some error occured')


    });

  }// end get previous chat with any user


  public loadEarlierPageOfChat: any = () => {

    this.loadingPreviousChat = true;

    this.pageValue++;
    this.scrollToChatTop = true;
    console.log("scroll top "+this.scrollToChatTop);
    this.getPreviousChatWithAUser() 

  } // end loadPreviousChat



public getMessageFromAUser :any =()=>{
 
  console.log("from the users");
  this.SocketService.chatByUserId(this.userInfo.userId)
  .subscribe((data)=>{
    console.log("from the users2");

    (this.receiverId==data.senderId)?this.messageList.push(data):'';

    this.toastr.success(`${data.senderName} says : ${data.message}`)

    this.scrollToChatTop=false;
    console.log("scroll top "+this.scrollToChatTop);
  });//end subscribe

}// end get message from a user 



public logout: any = () => {

  this.AppService.logout()
    .subscribe((apiResponse) => {

      if (apiResponse.status === 200) {
        console.log("logout called")
        Cookie.delete('authtoken');

        Cookie.delete('receiverId');

        Cookie.delete('receiverName');

        this.SocketService.exitSocket()

        this.router.navigate(['/']);

      } else {
        this.toastr.error(apiResponse.message)

      } // end condition

    }, (err) => {
      this.toastr.error('some error occured')


    });

} // end logout


parentEmitter(name:String):any{
this.toastr.success("you are chatting with"+name);
}


}

